body {
    font-family: 'RegularFont', sans-serif;
    /* Fallback to sans-serif if RegularFont fails to load */
  }

@font-face {
    font-family: 'logoFont';
    src: url('./assets/fonts/good-times.otf') format('opentype');
    /* Add more src definitions for different font file formats if needed */
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'HeaderFont';
    src: url('./assets/fonts/Eurostile\ Extended\ Regular/Eurostile Extended Regular.ttf') format('truetype');
    /* Add more src definitions for different font file formats if needed */
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'RegularFont';
    src: url('./assets/fonts/Carmen\ Sans\ Thin.otf') format('opentype');
    /* Add more src definitions for different font file formats if needed */
