.icon {
    transition: margin-top 0.3s ease;
    /* Add transition for smooth effect */
}

.icon:hover {
    margin-top: -5px;
    color: rgb(132, 232, 162);
    /* Move the icon slightly upward on hover */
}

.hoverColor:hover {
    color: rgb(132, 232, 162);
/*    color: rgb(240, 96, 122);   */
}

.keyFeature:hover {
    border: 2px solid rgb(132, 232, 162);
    cursor: pointer;
}

.content-container {
    padding: 10px 0px 10px;
    border-radius: 15px;
    background-color: #224651;
    justify-content: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes typingEffect {
    from { width: 0; }
    to { width: 100%; }
}

@keyframes colorChange {
    0% { color: #ddd; }
    50% { color: #a4da89; } /* Intermediate color for effect */
    100% { color: #84E8A2; }
}

@keyframes pulseGlow {
    0% { box-shadow: 0 0 8px #84E8A2; }
    50% { box-shadow: 0 0 16px #84E8A2; }
    100% { box-shadow: 0 0 8px #84E8A2; }
}

.fadeInUp {
    animation: fadeInUp 2s ease-out forwards;
}

.typingEffect {
    display: block;
    white-space: normal;
    word-wrap: break-word;
    overflow: hidden;
    border-right: 3px solid orange;
    
    animation: 
        typingEffect 3.5s steps(40, end) forwards,
}

.pulseGlow {
    animation: pulseGlow 2s infinite ease-in-out;
}